export const getRandomInt = (min, max) => {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min)) + min; //ìµœëŒ“ê°’ì€ ì œì™¸, ìµœì†Ÿê°’ì€ í¬í•¨
};

export const getRandomIntInclusive = (min, max) => {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min + 1)) + min; //ìµœëŒ“ê°’ë„ í¬í•¨, ìµœì†Ÿê°’ë„ í¬í•¨
};

export const shuffle = (array) => {
  for (let index = array.length - 1; index > 0; index--) {
    // ë¬´ì‘ìœ„ index ê°’ì„ ë§Œë“ ë‹¤. (0 ì´ìƒì˜ ë°°ì—´ ê¸¸ì´ ê°’)
    const randomPosition = Math.floor(Math.random() * (index + 1));
    // ì„ì‹œë¡œ ì›ë³¸ ê°’ì„ ì €ì¥í•˜ê³ , randomPositionì„ ì‚¬ìš©í•´ ë°°ì—´ ìš”ì†Œë¥¼ ì„ëŠ”ë‹¤.
    const temporary = array[index];
    array[index] = array[randomPosition];
    array[randomPosition] = temporary;
  }
};
